import * as React from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import { useMediaQuery, useTheme } from '@mui/material';

interface ListItem {
    title: string;
    img: string;
    author: string;
}

interface Props {
    itemData: ListItem[];
    hours?: boolean;
}

export default function TitlebarBelowImageList({ itemData, hours }: Props) {
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	return (
		<ImageList sx={{ maxHeight: isSmUp ? 2400 : 2400 }} variant="masonry" cols={isSmUp ? 3 : 2} gap={8}>
			{itemData.map((item) => {
				const hoursInfo = hours ? <span>&middot; {Math.round(Math.random() * 10)} hours ago</span> : null;
				return <ImageListItem key={item.img}>
					<img
						srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
						src={`${item.img}?w=248&fit=crop&auto=format`}
						alt={item.title}
						loading="lazy"
					/>
					<ImageListItemBar
						title={item.title}
						subtitle={<><span>by: {item.author}</span>{hoursInfo}</>}
						position="below"
					/>
				</ImageListItem>;
			})}
		</ImageList>
	);
}

