import React from 'react';
import {
	Avatar,
	Box,
	Divider,
	IconButton,
	ListItemIcon,
	Menu,
	MenuItem,
	Tooltip,
	useMediaQuery
} from '@mui/material';
import {Theme, ThemeMode} from '../types';
import {css} from '@emotion/css';
import {Logout} from '@mui/icons-material';
import useAuth from './Auth';
import KeyIcon from '@mui/icons-material/Key';
import { useNavigate } from 'react-router-dom';
import { useThemeContext } from '../ThemeProvider';
import MaterialUISwitch from './MuiThemeSwitch';

const AccountMenu = () => {
	const isSmScreen = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
	const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
	const open = Boolean(anchorEl);
	const {userProfile, logout, isAuthenticated, loginWithRedirect} = useAuth();
	const navigate = useNavigate();
	const {toggleDarkMode, isDarkModeOn} = useThemeContext();

	const handleClick = (event: React.MouseEvent<HTMLElement>) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};
	const handleLogoutClick = () => {
		logout();
	};
	const {theme} = useThemeContext(); // TODO: rename var to themeMode

	let initials = '';
	if (isAuthenticated) {
		initials = userProfile.firstName && userProfile.lastName 
			? `${userProfile.firstName[0]}${userProfile.lastName[0]}`.toUpperCase() 
			: `${userProfile.name[0]}${userProfile.name[1]}`.toUpperCase(); 
	}

	const logoSrc = theme === ThemeMode.Dark ? '/logo-dark.svg' : '/logo.svg';

	return <>
		<Box display='flex' justifyContent='right' alignItems='center'>
			<Tooltip title="Account settings">
				<IconButton
					onClick={handleClick}
					aria-controls={open ? 'account-menu' : undefined}
					aria-haspopup="true"
					aria-expanded={open ? 'true' : undefined}
					sx={{mt:1}}
				>
					<img src={logoSrc} width={isSmScreen ? 32 : 40} />
				</IconButton>
			</Tooltip>
		</Box>
		<Menu
			anchorEl={anchorEl}
			id="account-menu"
			open={open}
			onClose={handleClose}
		>
			<MenuItem onClick={() => navigate('/')}>
				Discover
			</MenuItem>
			{isAuthenticated && <MenuItem onClick={() => navigate('/profile')}>
				{userProfile.companyName}
			</MenuItem>}
			<MenuItem onClick={() => toggleDarkMode()}>
				<MaterialUISwitch checked={isDarkModeOn} />
				Switch to {isDarkModeOn ? 'light' : 'dark'}
			</MenuItem>
			{isAuthenticated && <Divider />}
			{isAuthenticated ? <MenuItem onClick={handleLogoutClick}>
				<ListItemIcon>
					<Logout fontSize="small" />
				</ListItemIcon>
				Sign out
			</MenuItem> : <MenuItem onClick={() => loginWithRedirect()}>
				<ListItemIcon>
					<KeyIcon fontSize="small"/>
				</ListItemIcon>
				Sign in
			</MenuItem>}
		</Menu>
	</>;
};

export default AccountMenu;
