import { Box, Toolbar, useTheme, AppBar } from '@mui/material';
import {css} from '@emotion/css';
import React, { useEffect, useRef, useState } from 'react';
import AccountMenu from './AccountMenu';

const PageHeader = () => {
	const theme = useTheme();
	const classes = {
		menu: css`
        & > div {
        }
        & > div > button.MuiButton-text {
          padding: ${theme.spacing(1, 2)};
          border-radius: 0;
        }
        `,
		hideAppBar: css`
			transform: translateY(-100%);
		`
	};

	const [showHeader, setShowHeader] = useState(true);
	const lastScrollY = useRef(0);
	const ticking = useRef(false); // To throttle updates
	const threshold = 10; // Minimum scroll delta to trigger visibility toggle

	useEffect(() => {
		const handleScroll = () => {
			const currentScrollY = window.scrollY;

			if (!ticking.current) {
				window.requestAnimationFrame(() => {
					const scrollDelta = currentScrollY - lastScrollY.current;

					if (Math.abs(scrollDelta) > threshold) {
						if (scrollDelta > 0) {
							// Scrolling down
							setShowHeader(false);
						} else {
							// Scrolling up
							setShowHeader(true);
						}
						lastScrollY.current = currentScrollY;
					}

					ticking.current = false;
				});
				ticking.current = true;
			}
		};

		window.addEventListener('scroll', handleScroll);

		return () => {
			window.removeEventListener('scroll', handleScroll);
		};
	}, []);

	return <>
		<AppBar position="fixed" color='transparent' style={{backgroundImage: 'none', boxShadow: 'none', transition: '0.3s ease'}} sx={{backgroundColor: 'background.default', opacity: 0.9}} className={showHeader ? '' : classes.hideAppBar}>
			<Toolbar>
				<Box display='flex' justifyContent='end' alignItems='center' sx={{width:'100%'}} className={classes.menu}>
					<Box flex={1} textAlign='right'>
						<AccountMenu />
					</Box>
				</Box>
			</Toolbar>
		</AppBar>
		<Toolbar />
	</>;
};

export default PageHeader;
