import {Theme, ThemeMode} from './types';
import {createTheme} from '@mui/material/styles';
import React, {createContext, ReactNode, useContext, useEffect, useState} from 'react';
import {ThemeProvider as EmotionThemeProvider} from '@emotion/react';

const emptyFn = () => {
	return;
};

const initialState = {
	isDarkModeOn: false,
	theme: ThemeMode.Light,
	toggleDarkMode: emptyFn,
};

const ThemeContext = createContext(initialState);

export function ThemeProvider({ children }: {children: ReactNode}) {
	const [dark, setDark] = useState(false); // Default theme is light

	// On mount, read the preferred theme from the persistence
	useEffect(() => {
		const isDark = localStorage.getItem('dark') === 'true';
		setDark(isDark);
	}, [dark]);

	// To toggle between dark and light modes
	const toggle = () => {
		const isDark = !dark;
		localStorage.setItem('dark', JSON.stringify(isDark));
		setDark(isDark);
	};

	const theme = dark ? darkTheme : lightTheme;
	
	return (
		<ThemeContext.Provider value={{ theme: dark ? ThemeMode.Dark : ThemeMode.Light, toggleDarkMode: toggle, isDarkModeOn: dark }}>
			<EmotionThemeProvider theme={theme}>
				{children}
			</EmotionThemeProvider>
		</ThemeContext.Provider>
	);
}

export const useThemeContext = () => useContext(ThemeContext);

const lightTheme: Theme = createTheme({
	typography: {
		fontFamily: 'Lexend, serif',
		h5: {
			fontFamily: 'Lexend Peta, serif',
			textTransform: 'uppercase',
			// color: '#444',
		},
		body1: {
			fontWeight: 300,
		},
		button: {
			
		},
	},
	palette: {
		mode: ThemeMode.Light,
		primary: {
			main: '#3A3042', // dark purple
		},
	},
});

const darkTheme: Theme = createTheme({
	typography: {
		fontFamily: 'Lexend, serif',
		h5: {
			fontFamily: 'Lexend Peta, serif',
			textTransform: 'uppercase',
			// color: '#444',
		},
		body1: {
			fontWeight: 300,
		},
		button: {
			
		},
	},
	palette: {
		mode: ThemeMode.Dark,
		primary: {
			main: '#ffc85e',
		},
	},
});
